@charset "UTF-8";

/* light theme color */
$light-background: #fff;
$light-background-secondary: #fff;
$light-color: rgba(76, 76, 76, 1);
$light-color-secondary: rgba(76, 76, 76, 1);
$light-border-color: #dcdcdc;
$light-hover: rgb(138, 138, 138);

/* dark theme colors */
$dark-background: #2b2c2c;
$dark-background-secondary: #2b2c2c;
$dark-color: #d5d9dc;
$dark-color-secondary: #d5d9dc;
$dark-border-color: #4a4b50;
$dark-hover: #d5d9dc86;

$media-size-phone: "(max-width: 684px)";
$media-size-tablet: "(max-width: 900px)";

/* variables for js, must be the same as these in @custom-media queries */
:root {
  --phoneWidth: (max-width: 684px);
  --tabletWidth: (max-width: 900px);
}
