.footer {
  padding: 40px 20px;
  flex-grow: 0;
  color: $light-color-secondary;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    width: 760px;
    max-width: 100%;

    @media #{$media-size-tablet} {
      flex-direction: column;
    }
  }

  &__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 1rem;
    color: $light-color;
    font-weight: 300;

    @media #{$media-size-tablet} {
      flex-direction: column;
    }

    & > *:not(:last-child) {
      border-right: 1px solid;
      padding: 0 15px;

      @media #{$media-size-tablet} {
        border: none;
      }
    }

    & > *:last-child {
      padding: 0 25px;
    }

    .dark-theme & {
      color: $dark-color;
    }
  }
}

@media #{$media-size-tablet} {
  .rss_logo {
    position: relative;
    top: 3px;
  }
}
