.posts {
  width: 100%;
  max-width: 800px;
  text-align: left;
  padding: 20px;
  margin: 20px auto;

  @media #{$media-size-tablet} {
    max-width: 660px;
  }

  &:not(:last-of-type) {
    border-bottom: 1px solid $light-border-color;

    .dark-theme & {
      border-color: $dark-border-color;
    }
  }

  &-group {
    display: flex;
    margin-bottom: 1.9em;
    line-height: normal;

    @media #{$media-size-tablet} {
      display: block;
    }
  }

  &-list {
    flex-grow: 1;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .post {
    &-title {
      font-size: 2rem;
      margin: 5px 0 5px 0;
      font-weight: 700;
    }

    &-content {
      font-family: "IBM Plex Mono", monospace;
      margin: 10px 0;
      overflow-wrap: break-word;

      @media #{$media-size-phone} {
        font-size: 0.8rem;
      }
    }

    &-year {
      padding-top: 6px;
      margin-right: 1.8em;
      font-size: 1.6em;

      @media #{$media-size-tablet} {
        margin: -6px 0 4px;
      }
    }

    &-tags {
      font-weight: 300;
      opacity: 1;
    }

    &-item {
      border-bottom: 1px grey dashed;

      a {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        padding: 10px 0;
        text-decoration: none;
      }
    }

    &-day {
      flex-shrink: 0;
      margin-left: 1em;
      font-weight: 200;
    }

    &-date {
      position: relative;
      bottom: 5px;
    }

    &-date:after {
      content: "";
    }
  }
}

.read-more {
  position: relative;
  bottom: 20px;
  font-weight: 700;
}

.read-more a {
  text-decoration: none;
}

.posts ul li {
  list-style-position: outside;
  list-style-type: none;
}

.post-terms-title {
  font-weight: 500;
}

.post-terms a {
  font-family: "IBM Plex Mono", monospace;
  font-weight: 400;
  text-decoration: none;
  line-height: 2rem;
}

.post-terms-tax {
  font-family: "IBM Plex Mono", monospace;
}
